.App {
  text-align: center;
  scroll-behavior: smooth
}

.App-logo {
  height: 60vh;
  max-width: 75%;
  pointer-events: none;
  margin-top: 5vh;
}

.App-header {
  background-color: #8fc29d;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content:first baseline;
  font-size: calc(10px + 2vmin);
  font-weight: bold;
  color: darkslategray;
}

.landingFont{
  font-family: 'SquareFont';
}